.gallery {
    padding-top: 20px;
    padding-bottom: 20px;
    @media screen and (min-width: 768px) {
      padding-top: 59px;
      padding-bottom: 40px;
    }
  
    @media screen and (min-width: 1280px) {
      padding-top: 60px;
      padding-bottom: 60px;
    }
  }
  
  .gallery__container {
    @extend .container;
  
    // min-height: 420px;
  
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 20px;
  
    @media screen and (min-width: 768px) {
      flex-direction: row;
      flex-wrap: wrap;
      gap: 32px;
      // min-height: 504px;
    }
  
    @media screen and (min-width: 1280px) {
      column-gap: 16px;
      row-gap: 32px;
      // min-height: 592px;
    }
  }