.movie__card {
    border-radius: 5px;
    overflow: hidden;
    box-shadow: 0 0 8px rgba(200, 200, 200, 0.8);
    transition: box-shadow 250ms linear;
  
    @media screen and (min-width: 768px) {
      width: calc((100% - 32px) / 2);
    }
  
    @media screen and (min-width: 1280px) {
      width: calc((100% - 2 * 16px) / 3);
    }
  }
  
  .movie__thumb {
    border-radius: 5px;
    overflow: hidden;
  
    @media (max-width: 767.98px) {
      width: 280px;
      height: 398px;
    }
  
    @media screen and (min-width: 768px) {
      width: 336px;
      height: 466px;
    }
  
    @media screen and (min-width: 1280px) {
      width: 395px;
      height: 574px;
    }
  }
  
  .movie__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 250ms linear;
  
    @media screen and (min-width: 768px) {
      .movie__card:hover &,
      .movie__card:focus & {
        transform: scale(1.05);
      }
    }
  }
  
  .movie__card:focus,
  .movie__card:hover {
    box-shadow: 0 0 16px rgba(0, 0, 0, 0.6);
    cursor: pointer;
  }
  
  .movie__info {
    padding: 8px 8px 0;
  
    @media screen and (min-width: 1280px) {
      padding-top: 12px;
    }
  }
  
  .movie__name {
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 500;
  }
  
  .movie__descr {
    color: #ff6b08;
    font-size: 20px;
    font-weight: 500;
  }